.showSearch {
  -webkit-animation: showSearch 0.5s forwards;
  -webkit-animation-delay: 1s;
  animation: showSearch 0.5s forwards;
  animation-delay: 1s;
}

.hideSearch {
  -webkit-animation: hideSearch 0.5s backwards;
  -webkit-animation-delay: 1s;
  animation: hideSearch 0.5s backwards;
  animation-delay: 1s;
}

@-webkit-keyframes a_width_left_to_right {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
@keyframes a_width_left_to_right {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
@keyframes shrink {
  0% {
    transform: scale(3);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes grow {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(3);
  }
}
@keyframes showSearch {
  0% {
    left: 38.3vw;
  }
  100% {
    left: 3.6vw;
  }
}
@keyframes hideSearch {
  0% {
    left: 3.6vw;
  }
  100% {
    left: 38.3vw;
  }
}
